import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { AlertController, LoadingController, ToastController } from '@ionic/angular';
import { AccessProviders } from '../providers/access-providers';
import { Geolocation } from '@awesome-cordova-plugins/geolocation/ngx';

@Component({
  selector: 'app-address-manager',
  templateUrl: './address-manager.page.html',
  styleUrls: ['./address-manager.page.scss'],
})
export class AddressManagerPage implements OnInit {
  
  name: string = "";
  contact_number: any = "";
  street: string = "";
  barangay_name: string = "";
  specific: string = "";
  latitude: any;
  longitude: any;
  agree = false;
  openProceed=false;
  coordinates=false;
  no=true;

  constructor(
    private router: Router,
    private toastController: ToastController,
    private loadingController: LoadingController,
    private alertController: AlertController,
    private geolocation: Geolocation,
    private accessProviders: AccessProviders
  ) { }

  ngOnInit() {
  }
  ionViewDidEnter() {
    this.openProceed=false;
    this.coordinates=false;
  }
  
  yesProceed(){
    this.coordinates=true;
  }
  noProceed(){
    if(this.coordinates==false){
      this.openProceed=true;
    }else{
      this.openProceed=true;
      this.coordinates=false;
    }

  
  }
  cancel(){
    this.openProceed=true;
    this.coordinates=false;
  }

  getCurrentCoordinates() {
    this.geolocation.getCurrentPosition({ enableHighAccuracy: true}).then((resp) => {
      this.latitude = resp.coords.latitude;
      this.longitude = resp.coords.longitude;
      console.log(this.latitude);
      console.log(this.longitude);
     }).catch((error) => {
       console.log('Error getting location', error);
     });
    this.openProceed=true;
  }

  async proceed() {
    if(this.name == "") {
      this.presentToast('Incomplete information!');
    } else if(this.contact_number == "") {
      this.presentToast('Incomplete information!');
    } else if(this.street == "") {
      this.presentToast('Incomplete information!');
    } else if(this.barangay_name == "") {
      this.presentToast('Incomplete information!');
    } else if(this.specific == "") {
      this.presentToast('Incomplete information!');
    }
    else {
      

      return new Promise(resolve => {
        let body = {
          aksi: 'process_address',
          name: this.name,
          contact_number: this.contact_number,
          street: this.street,
          barangay: this.barangay_name,
          specific: this.specific,
          latitude: this.latitude,
          longitude: this.longitude
        }
        this.accessProviders.postData(body, 'process_address.php').subscribe((res:any) => {
          if(res.success == true) {
            this.presentToast(res.msg);
            this.router.navigate(['/checkout']);
          } else {
            this.presentToast(res.msg);
          }
        }, (err) => {
          this.presentAlert('Timeout');
        });
      });
    }
  }

  async presentToast(a) {
    const toast = await this.toastController.create({
      message: a,
      duration: 1500,
      position: 'top'
    });
    toast.present();
  }

  async presentAlert(a) {
    const alert = await this.alertController.create({
      header: a,
      backdropDismiss: false,
      buttons: [
        {
          text: 'Cancel',
          role: 'cancel',
          cssClass: 'secondary',
          handler: (blah) => {
            console.log('Confirm Cancel: blah');
          }
        }, {
          text: 'Okay',
          handler: () => {
            console.log('Confirm Okay');
          }
        }
      ]
    });
  }

}

